'use strict'

const HttpRequest = require('httprequest');

const SolarSystemFactory = require('./Factory/SolarSystemFactory');

let dataRequest = new HttpRequest(
    'GET',
    '../data/solatsystem.json',
    true
);

let solarSystemData = null;

dataRequest.send().then((data) => {
    solarSystemData = data;
    console.log(data);
    let solarSystemFactory = new SolarSystemFactory(solarSystemData);

    solarSystemFactory.build(solarSystemData).then(() => {
        console.log('Success');
    })
})